.sideContainer{
    display: flex;
    .ListContainer{
        flex: 6;
    }
    .Top{
        box-shadow: 4px 3px 21px 0px rgba(0, 0, 0, 0.47);
        -webkit-box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        -moz-box-shadow: 4px 3px 21px 0px rgba(0, 0, 0, 0.58);
    }
    .Bottom{
    border-bottom: 1px solid lightgray;
    }
}
@media(max-width:415px){
    .Bottom{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
    }
}